import React from 'react';
import PropTypes from 'prop-types';

import { pdfjs, Document } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';

pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

const DashPdfDocument = (props) => {

    return (
        <Document file={props.file} >
            {props.children}
        </Document>
    );

};

DashPdfDocument.propTypes = {
    /**
     * File to be displayed, url or base64 string
     */
    file: PropTypes.string,

    /**
     * The ID used to identify this component in Dash callbacks
     */
    id: PropTypes.string,

    /**
     * Children of DashPdfDocument, list of DashPdfPage or other components
     */
    children: PropTypes.node,

};

export default DashPdfDocument;