import React, { useState, useCallback } from 'react';
import PropTypes from 'prop-types';

import { pdfjs, Page } from 'react-pdf';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';


pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

import AnnotationLayer from '../AnnotationLayer.js';

const DashPdfPage = (props) => {
    const [trigger, setTrigger] = useState(0);


    const onPageLoadSuccess = useCallback(async page => {
        const textContent = await page.getTextContent();
        let viewport = page.getViewport({ scale: 1 });

        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');


        const textItems = textContent.items.map(item => {
            // get item text
            const text = item.str;
            const transform = item.transform;
            const x = transform[4];
            const y = transform[5];
            const width = item.width;
            const height = item.height;


            ctx.font = item.height + "px " + textContent.styles[item.fontName].fontFamily

            // split text into words
            const words = text.split(' ');
            let wordBoxes = [];
            // measure word width for each word
            for (let i = 0; i < words.length; i++) {
                const word = words[i];
                // string before this word
                const before = item.str.substring(0, item.str.indexOf(word));
                const wordOffset = ctx.measureText(before).width;
                const wordWidth = ctx.measureText(word).width;
                const wordBox = {
                    word: word,
                    width: wordWidth,
                    height: item.height,
                    left: x + wordOffset,
                    top: y,
                }
                wordBoxes.push(wordBox);
            }
            return {
                text: text,
                transform: transform,
                x: x,
                y: y,
                width: width,
                height: height,
                words: wordBoxes,
            };

        }
        );
        props.setProps({ textItems: textItems });

    }, []);

    const updateSelection = (selection) => {
        props.setProps({ selection: selection });
    };

    return (
        <div {...props}>
            <Page onMouseUp={() => {
                setTrigger((trigger) => trigger + 1);
            }} pageNumber={props.pageNumber} width={props.width} renderAnnotationLayer={false} onLoadSuccess={onPageLoadSuccess}>
                <AnnotationLayer trigger={trigger} onSelection={updateSelection} userAnnotations={props.annotations} />
            </Page>

        </div>
    );
};


DashPdfPage.propTypes = {
    /**
     * Page number to be displayed
     */
    pageNumber: PropTypes.number,

    /**
     * Width of the page
     */
    width: PropTypes.number,

    /**
     * Annotations to be displayed
     * Example
     *         const annotation = {
            annotationFlags: 0,
            annotationType: 2,
            borderStyle: {
                width: 0,
                style: 1,
                horizontalCornerRadius: 0,
                verticalCornerRadius: 0,
            },
            rotation: 0,
            dest: "",
            contentsObj: {
                str: "",
                dst: "ltr",
            },
            subtype: "Link",
        }
        annotation.url = "obsidian://open?vault=Personal&file=university%2Fin%C5%BCynierka%2Ftest"
        annotation.rect = [left, top - height, left + width, top];
     */
    annotations: PropTypes.arrayOf(PropTypes.shape({
        annotationFlags: PropTypes.number,
        annotationType: PropTypes.number,
        borderStyle: PropTypes.shape({
            width: PropTypes.number,
            style: PropTypes.number,
            horizontalCornerRadius: PropTypes.number,
            verticalCornerRadius: PropTypes.number,
        }),
        rotation: PropTypes.number,
        dest: PropTypes.string,
        contentsObj: PropTypes.shape({
            str: PropTypes.string,
            dst: PropTypes.string,
        }),
        subtype: PropTypes.string,
        url: PropTypes.string,
        rect: PropTypes.arrayOf(PropTypes.number),
        id: PropTypes.string,
    })),

    /**
     * id of the component
     */
    id: PropTypes.string,

    /**
     * selection, annotation object, null if no selection
     */
    selection: PropTypes.shape({
        annotationFlags: PropTypes.number,
        annotationType: PropTypes.number,
        borderStyle: PropTypes.shape({
            width: PropTypes.number,
            style: PropTypes.number,
            horizontalCornerRadius: PropTypes.number,
            verticalCornerRadius: PropTypes.number,
        }),
        rotation: PropTypes.number,
        dest: PropTypes.string,
        contentsObj: PropTypes.shape({
            str: PropTypes.string,
            dst: PropTypes.string,
        }),
        subtype: PropTypes.string,
        url: PropTypes.string,
        rect: PropTypes.arrayOf(PropTypes.number),
        id: PropTypes.string,
    }),

    /**
     * text rects
     */
    textItems: PropTypes.arrayOf(PropTypes.shape({
        str: PropTypes.string,
        dir: PropTypes.string,
        rect: PropTypes.arrayOf(PropTypes.number),
        fontName: PropTypes.string,
        hasEOL: PropTypes.bool,
    })),


};

DashPdfPage.defaultProps = {
    pageNumber: 1,
    width: 500,
    annotations: [],
    selection: null,
    textItems: [],
};

export default DashPdfPage;
export const defaultProps = DashPdfPage.defaultProps;